.listAction{
    border:none;
    width:70px;
    height:70px;
    display:flex;
    justify-content: center;
    align-items: center;
    margin-left:10px;
    margin-right:10px;
    font-size:30px;
    background-color: #90A4AE;
    border-radius: 20px;
    color:#ffffff;
    transition: 0.5s;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
    @media(max-width:940px){
        width:60px;
        height:60px;
        font-size:25px;
    }
    @media(max-width:800px){
        width:50px;
        height:50px;
        font-size:20px;
       }
    &:hover{
        background-color: #F8BBD0;
        font-size: 32px;
       }
}